import TaskForm from './TaskForm';
import TaskList from './TaskList';

import { signOut } from 'firebase/auth';
import { auth } from '@/firebase';
import React, { useState } from 'react';

export default function Dashboard() {
  const [refresh, setRefresh] = useState(0);

  return (
    <div className="max-w-xl mx-auto mt-10 px-4">
      <h1 className="text-2xl font-bold mb-4 text-center">ðŸ§  Smart Task Manager</h1>
      
      <TaskForm onTaskCreated={() => setRefresh((prev) => prev + 1)} />
      <hr className="my-6" />
      <TaskList refreshTrigger={refresh} />

      {/* âœ… Logout Button */}
      <button
        onClick={() => signOut(auth)}
        className="block mx-auto mt-6 text-sm text-gray-500 underline"
      >
        Logout
      </button>
    </div>
  );
}
